// Font weight help class

.font-weight-medium {
  font-weight: 500;
}

// Social

.social-list-item {
  height: 2rem;
  width: 2rem;
  line-height: calc(2rem - 2px);
  display: block;
  border: 1px solid $gray-500;
  border-radius: 50%;
  color: $gray-500;
  text-align: center;
}

// Minimum Width Sizes
.w-xs {
  min-width: 80px;
}

.w-sm {
  min-width: 95px;
}

.w-md {
  min-width: 110px;
}

.w-lg {
  min-width: 140px;
}

.w-xl {
  min-width: 160px;
}

// Hover item
.item-hovered {
  &:hover {
    background-color: $gray-100;
  }
}

// Search bar
.search-bar {

  .form-control {
      border: none;
      height: $input-height;
      padding-left: 40px;
      padding-right: 20px;
      background-color: $search-bg;
      box-shadow: none;
      border-radius: 30px;
  }
  span {
      position: absolute;
      z-index: 10;
      font-size: 16px;
      line-height: $input-height;
      left: 13px;
      top: 0;
      color: $gray-600;
  }
}
